.catalog {
  position: relative;
}
.catalog__wrapper {
  padding-top: 10px;
}
.catalog__heading {
  font-size: 36px;
  line-height: 36px;
  color: #000000;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  margin-top: 4px;
  margin-bottom: 30px;
}
.catalog__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.catalog__item {
  padding-bottom: 25px;
}
.catalog__item::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin-top: -1px;
  border-bottom: 1px solid #ededed;
}
.catalog__item--wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
}
.catalog__item--link,
.catalog__item--info {
  width: 50%;
}
.catalog__item--link,
.catalog__item-image {
  display: block;
}
.catalog__item--title {
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  margin-top: 22px;
  margin-bottom: 12px;
  margin-right: 60px;
}
.catalog__table {
  width: 100%;
}
.catalog__order {
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-align: center;
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #68b738;
}
.catalog__order:hover {
  background-color: #5eaa2f;
}
.catalog__order:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .catalog__heading {
    font-size: 60px;
    line-height: 60px;
    margin-top: 2px;
    margin-bottom: 25px;
  }
  .catalog__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .catalog__item {
    box-sizing: border-box;
    width: 324px;
    padding-top: 58px;
    padding-bottom: 40px;
  }
  .catalog__item::before,
  .catalog__item::after {
    display: none;
  }
  .catalog__item--wrapper {
    position: relative;
  }
  .catalog__item--wrapper::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 82px;
    bottom: -40px;
    left: 0;
    right: 0;
    background-color: #f2f2f2;
  }
  .catalog__item-image {
    width: 324px;
    height: auto;
    position: absolute;
    bottom: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .catalog__item--link {
    width: 100%;
    min-height: 208px;
    position: relative;
  }
  .catalog__item--info {
    width: 100%;
    margin-left: 38px;
    margin-right: 37px;
    margin-bottom: 33px;
  }
  .catalog__item--title {
    font-size: 20px;
    text-align: center;
    width: 130px;
    margin: 38px auto 18px;
  }
  .catalog__order {
    box-sizing: border-box;
    width: 100%;
    margin-left: 38px;
    margin-right: 37px;
  }
}
@media (min-width: 1440px) {
  .catalog__heading {
    margin-top: 41px;
    margin-bottom: 22px;
  }
  .catalog__list {
    justify-content: flex-start;
  }
  .catalog__item {
    width: 245px;
    margin-right: auto;
  }
  .catalog__item:nth-child(4n) {
    margin-right: 0;
  }
}
.table {
